<script>
import conpous from "@/components/couponsAssembly.vue";
export default {
  data() {
    return {
      mask: false,
      showImg: [],
      show: false,
      coupons: [],
      count: false,
      goods: this.$router.params.goods,
      unqid: 0,
      activeInfo: {},
      brulecode: 0,
      //限时抢购倒计时
      seckill: {
        h: 0,
        m: 0,
        s: 0
      },
      offers: [],
      offset: -100,
      loading: true,
      finished: false,
      isLoading: false,
      goodsDetail: {},
      goodsDtl: "goodsDtl",
      prohibit: false,
      lazyImg: "....assetserror.png",
      discounts: [],
      showPopup: false,
      ruleCodeToText: {
        1110: "满减-现金",
        1120: "满减-包邮",
        1130: "满减-折扣",
        1210: "满赠-返利",
        1220: "满赠-包邮券",
        1230: "满赠-折扣券",
        1240: "满赠-赠品",
        1133: "团购",
        1113: "秒杀"
      }
    };
  },
  components: {
    conpous
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  mounted() {
    this.goodsDetail = JSON.parse(this.goods.detail);
    if (this.goods.vatp == -1) {
      this.prohibit = true;
    }
    this.getCoupons();
    this.getActive();
    this.getOfferTip();
    this.getLadoff();
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        this.loading = true;
        this.isLoading = false;
        this.goodsDtl = "goodsDtlEnd";
        setTimeout(() => {
          this.finished = false;
        }, 500);
      }, 500);
    },
    receiveCoupons() {
      this.show = !this.show;
      this.getCoupons();
    },
    couponBtn(data) {
      this.getCoupons();
    },
    viewImg() {
      this.showImg = [];
      this.showImg.push(this.goods.arr[0].imgURl);
      this.mask = !this.mask;
    },
    //  获取优惠券
    getCoupons() {
      JNB.query({
        serverName: "discountServer",
        cls: "CouponManageModule",
        method: "queryCouponPub",
        params: {
          compid: this.userInfo.comp.storeId,
          gcode: this.goods.sku
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.count = false;
            this.coupons = result.data;
            this.coupons.forEach(data => {
              data.btn = "立即领取";
              if (data.brulecode === 2110) {
                data.reduce = "减";
                data.symbol = "元";
                data.ladderVOS[0].offers = data.ladderVOS[0].offer;
              } else if (data.brulecode === 2120) {
                data.reduce = "包邮";
                if (data.ladderVOS[0].offer === 0) {
                  data.ladderVOS[0].offers = "";
                }
              } else {
                data.reduce = "";
                data.symbol = "折";
                data.ladderVOS[0].offers = data.ladderVOS[0].offer;
              }
            });
            if (this.coupons.length === 0) {
              this.count = true;
            }
          }
        }
      });
    },
    //  活动商品信息
    getActive() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CalculateModule",
        method: "getActivitiesBySKU",
        params: [this.goods.sku, this.userInfo.comp.storeId],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            if (result.data.discounts.length > 0) {
              //活动码
              this.unqid = result.data.discounts[0].unqid;
              this.getActiveInfo(result.data.discounts[0].brulecode);
            }
          }
        }
      });
    },
    //  活动商品详细信息
    getActiveInfo(brulecode) {
      let _this = this;
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CalculateModule",
        method: "getGoodsActInfo",
        params: [this.goods.sku, this.unqid],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            _this.activeInfo = result.data;
            this.$emit("activeInfo", result.data);
            if (result.data.ladoffs) {
              result.data.ladoffs.forEach(arr => {
                if (result.data.currNums >= arr.ladnum) {
                  _this.offers.push(arr.offer);
                }
              });
              if (_this.offers.length < 1) {
                _this.offers = [0];
              } else {
                _this.offers = _this.offers.sort();
              }
            }
            this.secondKill(
              this.$stringToDate(result.data.currentDate),
              result.data.endTime
            );
            _this.brulecode = brulecode;
          }
        }
      });
    },
    //  活动信息
    getLadoff() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CalculateModule",
        method: "getLadoff",
        params: [this.goods.sku],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.discounts = result.data;
          }
        }
      });
    },
    //  倒计时 时间转换
    secondKill(date, eDate) {
      let endDate = this.$stringToDate(
        date.getFullYear() +
          "-" +
          (Number(date.getMonth()) + 1) +
          "-" +
          date.getDate() +
          " " +
          eDate
      );
      let times = Math.floor((endDate - date) / 1000);
      let _this = this;
      if (times >= 0) {
        let timer;
        timer = setInterval(function() {
          times--;
          _this.seckill.h =
            Math.floor(times / 60 / 60) >= 10
              ? Math.floor(times / 60 / 60)
              : "0" + Math.floor(times / 60 / 60);
          _this.seckill.m =
            Math.floor(times / 60) % 60 >= 10
              ? Math.floor(times / 60) % 60
              : "0" + (Math.floor(times / 60) % 60);
          _this.seckill.s = times % 60 >= 10 ? times % 60 : "0" + (times % 60);
          if (times <= 0) {
            clearInterval(timer);
          }
        }, 1000);
      }
    },
    onLoad() {
      this.goodsDtl = "goodsDtl";
      this.loading = false;
      this.finished = true;
      this.backTop();
    },
    backTop() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    getOfferTip() {
      let arr = [
        {
          compid: this.userInfo.comp.storeId,
          pdno: this.goods.pdno,
          pnum: this.goods.num,
          checked: 1,
          unqid: this.goods.unqid
        }
      ];
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "ShoppingCartModule",
        method: "getOfferTip",
        params: arr,
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200 && result.data) {
            this.active = result.data;
            sessionStorage.setItem(
              "activeOrderList",
              JSON.stringify(result.data)
            );
          }
        }
      });
    },
    activeCode(data) {
      var rulecode = (data + "").substr(0, 4);
      var result = this.ruleCodeToText[rulecode];
      return result;
    },
    clickPopup() {
      if (this.discounts.length > 0) {
        this.showPopup = true;
      }
    }
  }
};
</script>

